<div class="device"><a class="title" :href="baseUri + device.link">{{ device.description }}</a><div class="thumb"><div class="top-right"><div><div class="vendor">{{ device.vendor }}<q-icon name="more" v-if="device.isWhiteLabel"><q-tooltip self="center end">White-Label Device</q-tooltip></q-icon></div></div></div><div class="is-new" v-if="isNew">new<q-tooltip self="center start">Added at {{ new Date(device.addedAt).toLocaleString() }}</q-tooltip></div><a class="device-img" :href="baseUri + device.link" :style="{ backgroundImage: 'url(' + baseUri + device.image + ')' }"></a></div><div class="desc"><div class="model">[{{ device.model }}]</div>{{ device.exposes.join(', ') }}</div></div>